.sectionHeaderWrapper{padding:clamp(1.5rem,5vw,4rem);position:relative;overflow:hidden;overflow:visible}.inner{position:relative;width:100%;height:10rem}.video{position:absolute;left:50%;top:50%;height:auto;transform:translate(-50%, -50%) translateY(20px);object-fit:cover;border-radius:1rem;box-shadow:0 0 1rem rgba(0,0,0,.3);pointer-events:none;z-index:1}.sectionHeaderText{position:relative;z-index:2;font-family:var(--font-cormorant);font-size:6rem;font-weight:600;line-height:1.1;color:#f0c332;text-align:left;max-width:100%}@media(max-width: 640px){.sectionHeaderText{font-size:4rem;text-align:center}}